import { gql } from '@apollo/client';

const GENERATE_SELLER_DOCUMENT_URL = gql`
  query generateUrl($fileId: ID!, $offerId: ID!, $propertyId: ID!) {
    getPropertyOfferPresignedDocument(
      FileId: $fileId
      OfferId: $offerId
      PropertyId: $propertyId
    ) {
      presignedUrl
    }
  }
`;

const GENERATE_BUYER_DOCUMENT_URL = gql`
  query generateUrl($fileId: ID!, $offerId: ID!) {
    getOfferPresignedDocument(FileId: $fileId, OfferId: $offerId) {
      presignedUrl
    }
  }
`;

export { GENERATE_SELLER_DOCUMENT_URL, GENERATE_BUYER_DOCUMENT_URL };
