// @flow
import clsx from 'clsx';
import React, { useEffect } from 'react';
import type { AbstractComponent, Element } from 'react';
import BlockUi from 'react-block-ui';
import { Loader } from 'react-loaders';
import { hideIfInvalidProps } from 'utils/RenderComponent';
import { Typography, List, ListItem, Link } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import _isEmpty from 'lodash/isEmpty';
import { useLazyQuery } from '@apollo/client';
import {
  GENERATE_SELLER_DOCUMENT_URL,
  GENERATE_BUYER_DOCUMENT_URL,
} from 'graphql/queries/documents/presigned';
import _get from 'lodash/get';
import { ListItemIcon } from '@material-ui/core';
import DescriptionOutlinedIcon from '@material-ui/icons/DescriptionOutlined';
import { camelcase } from 'helpers/Word';
import type { DocumentItemProps } from './helper';

const useStyles = makeStyles(theme => ({
  link: {
    ...theme.typography.h5,
    display: 'flex',
    color: theme.palette.text.dark,
  },
  list: {
    marginLeft: '0px',
  },
  listItem: {
    padding: '4px 0',
  },
  listItemIcon: {
    minWidth: '34px',
    '& path': {
      fill: theme.palette.common.gray,
    },
  },
  address: {
    ...theme.typography.h6,
    color: theme.palette.text.secondary,
    flex: 1,
  },
  linkHighlight: {
    fontWeight: '900',
  },
}));

const checkValidProps = ({ documents }) => !_isEmpty(documents);

type Props = DocumentItemProps & { isSeller: boolean };

export const DocumentItem = (props: Props): Element<typeof BlockUi> => {
  const { address, documents, offerId, propertyId, isSeller } = props;
  const classes = useStyles();

  const [onClickHandler, { loading, data }] = useLazyQuery(
    isSeller ? GENERATE_SELLER_DOCUMENT_URL : GENERATE_BUYER_DOCUMENT_URL,
  );

  useEffect(() => {
    const queries = isSeller
      ? 'getPropertyOfferPresignedDocument.presignedUrl'
      : 'getOfferPresignedDocument.presignedUrl';
    const preSignedUrl = _get(data, queries);
    if (_isEmpty(preSignedUrl)) return;
    window.open(preSignedUrl, '_blank');
  }, [data, isSeller]);

  return (
    <BlockUi
      tag="div"
      blocking={loading}
      loader={<Loader active type="ball-pulse" color="rgba(231,28,99,1)" />}
      className={clsx(classes.container, { [classes.loading]: loading })}
    >
      <Typography className={classes.address}>{address}</Typography>
      <List className={classes.list}>
        {documents.map(document => {
          const { id: fileId, documentType } = document;
          return (
            <ListItem key={fileId} className={classes.listItem}>
              <ListItemIcon className={classes.listItemIcon}>
                <DescriptionOutlinedIcon />
              </ListItemIcon>
              <Link
                className={classes.link}
                href="#"
                onClick={() =>
                  onClickHandler({
                    variables: {
                      fileId,
                      offerId,
                      propertyId,
                    },
                    fetchPolicy: 'no-cache',
                  })
                }
              >
                {camelcase(documentType)}
              </Link>
            </ListItem>
          );
        })}
      </List>
    </BlockUi>
  );
};

export default (hideIfInvalidProps(checkValidProps)(
  DocumentItem,
): AbstractComponent<Props>);
