// @flow
import _isEmpty from 'lodash/isEmpty';
import _get from 'lodash/get';

export type DocumentItemProps = {
  documents: Array<Object>,
  propertyId: number,
  offerId: number,
  address: string,
};

export const getSellerDocuments = (data: Object): Array<DocumentItemProps> => {
  const items = _get(data, 'getUserOverview.Properties.Items') || [];
  return items.reduce((list, item) => {
    const offer = _get(item, 'AcceptedOffer') || {};
    const documents = _get(offer, 'Documents') || [];

    if (_isEmpty(documents)) return list;

    return [
      ...list,
      {
        documents,
        propertyId: _get(item, 'PropertyId'),
        offerId: _get(offer, 'OfferId'),
        address: _get(item, ['PropertyData', 'FullAddress']),
      },
    ];
  }, []);
};

export const getBuyerDocuments = (data: Object): Array<DocumentItemProps> => {
  const items = _get(data, 'getUserOverview.Offers.Items') || [];
  return items.reduce((list, item) => {
    const documents = _get(item, 'Documents') || [];

    if (_isEmpty(documents)) return list;

    return [
      ...list,
      {
        documents,
        propertyId: _get(item, 'Property.PropertyId'),
        offerId: _get(item, 'OfferId'),
        address: _get(item, 'Property.PropertyData.FullAddress'),
      },
    ];
  }, []);
};
