// @flow
import { useQuery } from '@apollo/client';
import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import _isEmpty from 'lodash/isEmpty';
import React, { useEffect, Fragment, useContext } from 'react';
import type { Element } from 'react';
import BlockUi from 'react-block-ui';
import { Loader } from 'react-loaders';
import {
  GET_SELLER_DOCUMENTS,
  GET_BUYER_DOCUMENTS,
} from 'graphql/queries/user/overview';
import { toastr } from 'react-redux-toastr';
import { PROPERTY_ERROR_MESSAGE } from 'shared/Messages';
import { withErrorBoundary } from 'components/ErrorBoundary';
import UserContext from 'contexts/UserContext';
import { Widget } from 'components/Widget';
import DocumentItem from './DocumentItem';
import { getSellerDocuments, getBuyerDocuments } from './helper';

const useStyles = makeStyles(theme => ({
  container: {
    minHeight: '100px',
  },
  loading: {
    border: '1px solid #E0E0E0',
    backgroundColor: theme.palette.common.white,
  },
}));

export const DocumentWidget = (): Element<'div' | typeof Fragment> => {
  const classes = useStyles();
  const { IsSeller: isSeller } = useContext(UserContext);
  const { loading, error, data } = useQuery(
    isSeller ? GET_SELLER_DOCUMENTS : GET_BUYER_DOCUMENTS,
    {
      fetchPolicy: 'cache-first',
    },
  );

  useEffect(() => {
    const { message } = error || {};
    if (message) {
      toastr.error(PROPERTY_ERROR_MESSAGE.fetchFailure, message);
    }
  }, [error]);

  const documentsData = isSeller
    ? getSellerDocuments(data)
    : getBuyerDocuments(data);

  if ((_isEmpty(data) || _isEmpty(documentsData)) && !loading) {
    return <></>;
  }

  return (
    <BlockUi
      tag="div"
      blocking={loading}
      loader={<Loader active type="ball-pulse" color="rgba(231,28,99,1)" />}
      className={clsx(classes.container, { [classes.loading]: loading })}
    >
      <Widget title="Documents">
        {documentsData.map(data => {
          const { propertyId, offerId, address, documents } = data;
          return (
            <DocumentItem
              key={`document-${propertyId}-${offerId}`}
              propertyId={propertyId}
              offerId={offerId}
              address={address}
              documents={documents}
              isSeller={isSeller}
            />
          );
        })}
      </Widget>
    </BlockUi>
  );
};

type Props = {};

export default (withErrorBoundary(DocumentWidget): Element<Props>);
